@import url('https://fonts.googleapis.com/css2?family=Playfair+Display&family=Quicksand&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Margarine&family=Poppins:wght@200&family=Roboto+Slab&display=swap');

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'f01';
  src: url('/src/assets/font/KouzanBrushFontSousyo.ttf');
}